import { toJS } from 'immutable'
import {
  createImmutableSelector
} from './index';

const getRouter = state => state.get('router');

export const getRouterLocationSelector = createImmutableSelector(
  getRouter,
  (router) => {
    return router.get('location').toJS();
  }
);



